import { Component } from "@angular/core";
import { NavController, NavParams } from "ionic-angular";

import { AppEvents } from "../../../helpers/events";
import { AppData } from "../../../models/data";

import { ConfigurationService } from "../../../providers/configuration";

@Component({
	selector: "page-book-options",
	templateUrl: "options.html"
})
export class ReadingOptionsPage {
	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public configSvc: ConfigurationService
	){
	}

	// attributes
	info = {
		title: "Tu chn c",
		options: AppData.Configuration.reading.options
	};
	options = {
		colors: [
			{
				label: "Trng",
				value: "white"
			},
			{
				label: "en",
				value: "black"
			},
			{
				label: "Ng vng",
				value: "sepia"
			}
		],
		fonts: [
			{
				label: "Mc nh",
				value: "default"
			},
			{
				label: "Gn mc nh",
				value: "like-default"
			},
			{
				label: "Trn",
				value: "plump"
			},
			{
				label: "Bo",
				value: "fat"
			},
			{
				label: "Khc l",
				value: "fancy"
			}
		],
		sizes: [
			{
				label: "Nh nht",
				value: "smallest"
			},
			{
				label: "Nh hn",
				value: "smaller"
			},
			{
				label: "Nh",
				value: "small"
			},
			{
				label: "Trung bnh",
				value: "normal"
			},
			{
				label: "To",
				value: "big"
			},
			{
				label: "To hn",
				value: "bigger"
			},
			{
				label: "Rt l b",
				value: "huge"
			}
		],
		paragraphs: [
			{
				label: "Bnh thng",
				value: "one"
			},
			{
				label: "Tha",
				value: "two"
			},
			{
				label: "Rt tha",
				value: "three"
			}
		],
		aligns: [
			{
				label: "Tri",
				value: "align-left"
			},
			{
				label: "u hai bn",
				value: "align-justify"
			},
			{
				label: "Phi",
				value: "align-right"
			}
		]
	};

	// events
	ionViewWillUnload() {
		window.setTimeout(async () => {
			await this.configSvc.saveOptionsAsync(() => {
				AppEvents.broadcast("ReadingOptionsAreUpdated");
			});
		});
	}

}


